import { useEffect } from 'react'
import { Modal,Button } from "react-bootstrap";

const Hedstyle = {
  padding: '14px 0'
}
export const ProjectModal = (props) => {
  const data = props.element;
  // console.log(data)
  const array = data.imgs.map((ele,index) => {
    return (
      <div class={`carousel-item ${index === 0 ? 'active' : ''}`}>
        <img src={ele} className="d-block w-100 inv" alt=""  style={{maxHeight:'400px',objectFit:'contain'}}/>
      </div>
  )})
  const buttons = data.imgs.map((ele,index) => {
    if (index === 0 ){
      return <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to={index} aria-label={`Slide ${index+1}`} class="active" aria-current="true" ></button>
    }else
      return <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to={index} aria-label={`Slide ${index+1}`} ></button>
  })
  
  useEffect(() => {
    let temp = document.getElementsByClassName('inv')
    for(let i = 0; i < temp.length; i += 1) {
      temp.item(i).classList.toggle("inverse-dark");
      console.log(temp[i])
    }
    
  }, [])
  let aa;
  let styleh4 = {
    paddingTop:'10px',color:'#e96224'
  }
  if(data.url != "")
    aa = <h4 style={styleh4}><a style={{color:'#e96224'}} href={data.url}>{data.subhead}</a></h4>
  else
    aa = <h4 style={styleh4}>{data.subhead}</h4>
  return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            {data.name}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
        <div id="carouselExampleCaptions" class="carousel carousel-dark slide" data-bs-ride="carousel">
        <div class="carousel-indicators">
          {buttons}
        </div>
        <div class="carousel-inner">
          {array}
        </div>
        <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="prev">
          <span class="carousel-control-prev-icon" aria-hidden="true"></span>
          <span class="visually-hidden">Previous</span>
        </button>
        <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="next">
          <span class="carousel-control-next-icon" aria-hidden="true"></span>
          <span class="visually-hidden">Next</span>
        </button>
        </div>
        {aa}
        <p>&emsp;{data.desc}</p>
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={props.onHide}>Close</Button>
        </Modal.Footer>
      </Modal>
    ); 
}