import React from 'react'
import { ProjectModal } from '../layouts/Modal.js'


const Project = ({id, name, url, skills,imgs,desc,subhead}) => {
    const [modalShow, setModalShow] = React.useState(false);
    const properties = {id, name, url, skills,imgs,desc,subhead}
    
    return (
        <div data-aos="fade-up" className=" col-12 col-lg-4 project-card">
            <div className="image-project">
                <div className={`img-pro ${id}`}></div>
            </div>
            <a title={name} className="project-name" rel="noopener noreferrer" target="_blank" 
            onClick={() => setModalShow(true)} style={{cursor:'pointer'}}>
                <h2>{name}</h2></a>
            { skills && <h3>{skills.map(skill => skill)}</h3> }
            <ProjectModal
                show={modalShow}
                onHide={() => setModalShow(false)}
                element={properties}
                />
        </div>
    )
}

export default Project
